import styles from "./Dashboard.module.css";
import Graph3 from "./Graph3.js";
import Transactions from "./Transactions.js"
import classnames from "classnames";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import besideIcon from "../assets/clock.png";
import icon7 from "../assets/cake.png"
import icon8 from "../assets/coin2.png"
import Slider from "./Slider.js";
import React from "react";
import { useSelector, useDispatch} from "react-redux";
import Drop from "./Drop.js"
import groupData from "../utils/constants/groupData.js"

import  {getAllUserClaimableRounds,claimAllPredictions,getGroupData,hasStopped,getCurrentTimestamp,getCandleChartData,isClaimable,getPriceChartData,getCurrentRoundNo,setPredictionIndex ,getCurrentRoundLockTimestamp,getPredictionIntervalSeconds,subscribePredictionEvents,getPriceFromOracle} from "../utils/contractData.js";



function Dashboard() {
  const web3 = useSelector(state => state.web3);
  const currentCurrency = useSelector(state => state.currentCurrency);
  const currentGroup= useSelector(state => state.currentGroup);
  const [ref,setRef]=React.useState(true);

  var userAddress=useSelector(state=>state.userAddress);
  var collectShow=styles.hide;
  if(userAddress)
  collectShow=null;
  const [str22,setStr22]=React.useState("Collect");
  const [total2,setTotal2]=React.useState("0");
  const [disable22,setDisable22]=React.useState(false);

  React.useEffect(()=>{
    getAllUserClaimableRounds(userAddress).then((res)=>{
      setTotal2(res.totalUsdAmount.toString());
    })
  },[ref])
  
  async function f(){
    dispatch({type:"calculatedChange", calculated:true});
    var keys = Object.keys( groupData );
   
    const gData = await getGroupData(keys[currentGroup]);
    console.log("ids")
    console.log(gData)
    var predictionIndex=gData[0]
    console.log("predictionindex "+predictionIndex)
    await setPredictionIndex(predictionIndex);
    dispatch({type:"currencyChange", currentCurrency:predictionIndex});
dispatch({type:"betChange"});
var stoped=await hasStopped();

      dispatch({type:"pausedChange", paused:stoped});
     
     
const timestampLeft = await getCurrentRoundLockTimestamp();
      console.log(timestampLeft)
      var nowTime=await getCurrentTimestamp()
      var timeSync=(Date.now()/1000)-nowTime;
      dispatch({type:"timeSyncChange",timeSync:timeSync})
      var timer = Math.ceil(timestampLeft - nowTime)
      //var timer = Math.ceil(timestampLeft - (nowTime)); 
      console.log("timer"+timer); 
      dispatch({type:"timeLeftChange", timeLeft:timestampLeft});
      var roundDuration = await getPredictionIntervalSeconds();
      dispatch({type:"durationChange", duration:roundDuration});
     var currentRound=await getCurrentRoundNo();
        dispatch({type:"numChange", num:currentRound}); 
       
dispatch({type:"chartDataChange", chartData:[1]});
        
      const subscribeEventHandler = (eventName,roundNo) => { 
        console.log("subscribe events")
        switch(eventName) {
        
        case "PAUSED": 
        dispatch({type:"pausedChange", paused:true});
        console.log("PAUSED")
        break;
        case "STARTROUND":
          console.log("STARTROUND")
 
        break;
        case "REWARDSCALCULATED":
          console.log("STARTROUND")
          getGroupData(keys[currentGroup]).then((ids)=>{
            console.log("ids")
            console.log(ids)
            setPredictionIndex(ids[0]).then(()=>{
              dispatch({type:"currencyChange", currentCurrency:ids[0]});

              subscribePredictionEvents(subscribeEventHandler).then(()=>{});
               hasStopped().then((stopped)=>{
                dispatch({type:"pausedChange", paused:stoped});
               });
               getCurrentRoundNo().then((currentRound)=>{
               dispatch({type:"numChange", num:currentRound}); 
               getCurrentTimestamp().then((nowTime)=>{
           getCurrentRoundLockTimestamp().then((timestampLeft)=>{
                  var timer = Math.ceil(timestampLeft - (nowTime)); 
                  console.log("timer"+timer); 
                  dispatch({type:"timeLeftChange", timeLeft:timestampLeft});
                  dispatch({type:"betChange"})
                  dispatch({type:"calculatedChange", calculated:true})
                })
               })   
            })        
            })
          })
    
        console.log("REWARDSCALCULATED")
        
          break;
           
        default: }
        };
    subscribePredictionEvents(subscribeEventHandler);
  }
   React.useEffect(() => {
  f();

  }, [currentGroup]);
  const dispatch = useDispatch();
  const tokens = useSelector(state => state.tokens);
  const [currentIcon,setCurrentIcon] = React.useState(null);
  const [currentName,setCurrentName] = React.useState("");
  React.useEffect(()=>{
if(tokens!=0)
{
  var token =tokens[currentGroup];
  setCurrentName(token);
  //var token="BNB"
  if(token.toUpperCase()=="BNB")
  setCurrentIcon(icon1) 
  else 
  if(token.toUpperCase()=="BTC")
  setCurrentIcon(icon2)
  else if(token.toUpperCase()=="ETH")
  setCurrentIcon(icon3)
  else if(token.toUpperCase()=="APPLE"||token.toUpperCase()=="APL")
  setCurrentIcon(icon4)
  else if(token.toUpperCase()=="GOOGLE"||token.toUpperCase()=="GOOGL")
  setCurrentIcon(icon5)
  else if(token.toUpperCase()=="TESLA"||token.toUpperCase()=="TSL"||token.toUpperCase()=="TSLA")
  setCurrentIcon(icon6)
  else if(token.toUpperCase()=="CAKE")
  setCurrentIcon(icon7)
  else
  setCurrentIcon(icon8)
}

  },[currentGroup,tokens])
 
  
   
    return (<div className={styles.dash}>

  
      <i className={classnames("fas fa-bars",styles.menuImg)} onClick={()=>{
dispatch({ type: "showMenuChange"});
      }}></i>
<div className={classnames(styles.dashbooard,styles.dashbooard_s, "font_22","font_22_s")}>Dashboard</div>
<hr className={styles.hr}></hr>

<div> <Drop show={null}/></div>
<div className={styles.row}>
  <div className={styles.c3}><div className={styles.menu_item3}>

    </div></div>
    <div className={styles.c1}>&nbsp; </div>
        <div className={styles.c2}>
        <div className={classnames(styles.menu_item1)}>
          <img src={currentIcon} className={styles.icon1}></img>
          <span className={classnames(styles.menu_text1,"font_21","font_4_s","font_2_m")}>{currentName} <Price/>$</span>
        </div>
        <div className={collectShow}>
        <div className={styles.csspan}><button className={styles.caButton} disabled={disable22} onClick={() => {
            setStr22("Collecting");
            setDisable22(true);

            claimAllPredictions(web3, userAddress).then((result) => {
              setDisable22(false);
              setStr22("Collect");
              if (result.status == true) {
                setRef(!ref);
              }  
            })

          }}>{str22}</button><span className={styles.myspan}>&nbsp; {total2}$</span></div>
        </div>
        </div>
        {/*<div className={styles.c2}>
        <div className={classnames(styles.menu_item2)}>
          <img src={besideIcon} className={styles.icon2}></img>
          <Timer/>
    </div>
    </div>*/}
      </div>
<Slider/>


<Graph3/>
<div ><Transactions/></div>

    </div>);
}
export default Dashboard;

function Timer()
{
  const currentTime = useSelector(state => state.timerValue);

  return(
<span className={classnames(styles.menu_text2,"font_23","font_30_s","font_25_m")}>{currentTime}</span>
  );
}

function Price()
{
  const [tokenPrice,setTokenPrice]=React.useState(0);
  React.useEffect(()=>{
  
    const interval=setInterval(function(){
      getPriceFromOracle().then((price)=>{
        price=(Number(price).toFixed(3));
setTokenPrice(price);
      })
      }, 3000);
     

     return () => clearInterval(interval);

  },[])
  return(
<span>{tokenPrice}</span>
  );
}