import styles from "./History.module.css";
import classnames from "classnames";
import Chart from "./Chart.js";
import { useSelector, useDispatch } from "react-redux";
import { claimSelectedPredictions,getAllUserClaimableRounds,getGroupClaimableRounds,getAllGroupUserClaimableRounds,getTotalClaimableReferenceBonus,claimAllPredictions,getRoundData,getUserHistory, claim, getUserClaimableRounds } from "../utils/contractData.js";
import { useEffect, useState } from "react";
import Drop from "./Drop.js"
import image5 from "../assets/8311-loading.json";
import lottie from "lottie-web";
import medal from "../assets/medal.png"
import { style } from "dom-helpers";
import groupData from "../utils/constants/groupData.js"


function History() {

  const currentCurrency = useSelector(state => state.currentCurrency);
  const currentGroup = useSelector(state => state.currentGroup);
  const userAddress = useSelector(state => state.userAddress);
  const web3 = useSelector(state => state.web3);
  const [addCss1, setaddCss1] = useState(null);
  const [addCss2, setaddCss2] = useState(null);
  const [addCss3, setaddCss3] = useState(styles.hide);
  const [addCss4, setaddCss4] = useState(styles.hide);
  const [history, setHistory] = useState(null);
  const [rounds, setRounds] = useState([]);
  const [total, setTotal] = useState(null);
  const [predictionIds,setPredictionIds]=useState([]);
  const [predictionRounds,setPredictionRounds]=useState([]);
  const [total2, setTotal2] = useState(null);
  const [ref, setRef] = useState(true);
  const [str, setStr] = useState("Collect");
  const [disable, setDisable] = useState(false);
  const [str22, setStr22] = useState("Collect All");
  const [disable22, setDisable22] = useState(false);
  const [radio, setRadio] = useState("1");
  useEffect(() => {
    lottie.loadAnimation({
      container: document.querySelector("#anim"),
      animationData: image5,
    });


  }, []);

  useEffect(() => {


    if (userAddress == null) {
      setaddCss2(styles.hide);
      setaddCss1(null);
      setaddCss3(styles.hide);
      setaddCss4(styles.hide);
    }
    if (userAddress != null) {
      setaddCss1(styles.hide)
      setaddCss2(styles.hide);
      setaddCss4(styles.hide);
      setaddCss3(null);
      var keys = Object.keys( groupData );
      console.log(keys[currentGroup])
      console.log("getting history")
      getUserHistory(keys[currentGroup], userAddress).then((result) => {
        console.log("get new history");
        console.log(result)
        setaddCss3(styles.hide);
        if (result == null || result.numRounds == 0) {
          setaddCss4(null);
          setaddCss1(styles.hide)
          setaddCss2(styles.hide)
          setaddCss3(styles.hide)
        }
        else {
          setaddCss4(styles.hide);
          setaddCss1(styles.hide)
          setaddCss2(null)
          setaddCss3(styles.hide)
        }

console.log("new history")
console.log(result)
        setHistory(result);

      })
      getGroupClaimableRounds(keys[currentGroup], userAddress).then((res) => {
        setTotal(res.totalAmount);
        setRounds(res.claimableRounds);
        setPredictionIds(res.predictionIds);
        setPredictionRounds(res.predictionRounds);
      })
      getAllUserClaimableRounds(userAddress).then((res)=>{
        setTotal2(res.totalUsdAmount.toString());
      })

    }
  }, [currentGroup,currentCurrency, userAddress, ref])
  var bets = [];
  var numWon = 0;
  if (history) {

    for (var i = 0; i < history.numRounds; i++) {
      if (history.rounds[i].won == true)
        numWon++;
      var priceDiff = history.rounds[i].priceDiff;
      priceDiff = (Number(priceDiff).toFixed(3));
      // if(history.rounds[i].calculated==true)
      if (radio == "1") {
        bets.push(<Box key={history.rounds[i].prediction.predictionId +'.'+ i} round={history.rounds[i].roundNo} result={priceDiff} claim={history.rounds[i].claimed} won={history.rounds[i].won} roundData={history.rounds[i]}/>)
      }
      else if (radio == "2") {
        if (history.rounds[i].claimed == true && history.rounds[i].won == true)
          bets.push(<Box key={history.rounds[i].prediction.predictionId +'.'+ i} round={history.rounds[i].roundNo} result={priceDiff} claim={history.rounds[i].claimed} won={history.rounds[i].won} roundData={history.rounds[i]}/>)
      }
      else
        if (history.rounds[i].claimed == false && history.rounds[i].won == true)
          bets.push(<Box key={history.rounds[i].prediction.predictionId +'.'+ i} round={history.rounds[i].roundNo} result={priceDiff} claim={history.rounds[i].claimed} won={history.rounds[i].won} roundData={history.rounds[i]}/>)


    }
    var numLoss = history.numRounds - numWon;
  }

  var netEarning = null;
  var netEarningUSD = null;
  var average = null;
  var averageUSD = null;
  var best = null;
  var bestUSD = null;
  var avgBet = null;
  var avgBetUSD = null;
  var bettingTokenName="";

  if (history != null) {
    netEarning = history.netEarning;
    netEarningUSD = history.bettingTokenUSDPrice* history.netEarning
    netEarning = (Number(netEarning).toFixed(3));
    netEarningUSD = (Number(netEarningUSD).toFixed(3));
    average = history.avgReturn
    average = (Number(average).toFixed(3));
    averageUSD = history.avgReturn * history.bettingTokenUSDPrice
    averageUSD = (Number(averageUSD).toFixed(3));
    best = history.bestRound
    best = (Number(best).toFixed(3));
    bestUSD = history.bestRound * history.bettingTokenUSDPrice
    bestUSD = (Number(bestUSD).toFixed(3));
    avgBet = history.avgBet
    avgBet = (Number(avgBet).toFixed(3));
    avgBetUSD = history.avgBet * history.bettingTokenUSDPrice
    avgBetUSD = (Number(avgBetUSD).toFixed(3));
    bettingTokenName = history.bettingTokenName;
  }
  const dispatch = useDispatch();
  return (
    <div className={styles.dash}>
      <i className={classnames("fas fa-bars", styles.menuImg)} onClick={() => {
        dispatch({ type: "showMenuChange" });
      }}></i>
      <div className={classnames(styles.dashbooard, "font_22", "font_22_s")}>History</div>
      <hr className={styles.hr}></hr>

      <Drop show={addCss2} />
      <div id="anim" className={classnames(styles.anim, addCss3)}></div>
      <div className={classnames(styles.radioGroup, addCss2)} onChange={(event) => {
        setRadio(event.target.value)
      }}>

        <input type="radio" id="test1" name="radio-group" value="1" defaultChecked />
        <label for="test1" className={styles.label}>All</label>


        <input type="radio" id="test2" name="radio-group" value="2" />
        <label for="test2" className={styles.label} >Collected</label>

        <input type="radio" id="test3" name="radio-group" value="3" />
        <label for="test3" className={styles.label} >Uncollected</label>

      </div>
      <div className={classnames(styles.row1, addCss1)}>Please connect to see your history!</div>
      <div className={classnames(styles.row1, addCss4)}>You have not any transaction in this prediction!</div>

      <div className={classnames(styles.row, addCss2)}>
        <div className={styles.c1_1}>

          <div className={styles.align}>

            {bets}


          </div>
          <div className={styles.row11}>
            <div className={styles.c1_1}>
            <div className={styles.collectAll}><div className={styles.csspan}><button className={styles.caButton} disabled={disable} onClick={() => {
            setStr("Collecting");
            setDisable(true);
            var keys = Object.keys( groupData );
           /* getAllGroupUserClaimableRounds(keys[currentGroup], userAddress).then((result) => {
              setDisable(false);
              setStr("Collect")
              if (result.status == true) {
                setRef(!ref);
              }
            })*/
            claimSelectedPredictions (web3, userAddress, predictionIds, predictionRounds).then((result)=>{
              setDisable(false);
              setStr("Collect")
              if (result.status == true) {
                setRef(!ref);
              }  
            })


          }}>{str}</button><span className={styles.myspan}>{total}$</span></div>


          
          </div>
            </div>
            <div className={styles.c2_2}>
            <div className={styles.collectAll}><div className={styles.csspan}><button className={styles.caButton} disabled={disable22} onClick={() => {
            setStr22("Collecting");
            setDisable22(true);

            claimAllPredictions(web3, userAddress).then((result) => {
              setDisable22(false);
              setStr22("Collect All")
              if (result.status == true) {
                setRef(!ref);
              }
            })

          }}>{str22}</button><span className={styles.myspan}>{total2}$</span></div>
          </div>
            </div>
          </div>

         
         
        </div>
        <div className={styles.c2_2}>
          <div className={classnames(styles.your, "font_25", "font_8_s", "font_32_m")}>
            Your History
          </div>
          <div className={classnames(styles.chart, "font_26")}>
            <div className={styles.c1111}> <div className={styles.chart2}><Chart numWon={numWon} numLoss={numLoss} /></div></div>
            <div className={styles.c2222, styles.beside}>
              <div className={classnames(styles.r111, "font_25", "font_8_s", "font_32_m")}>NET RESULT  </div>
              <div className={classnames(styles.r222, "font_27", "font_11_s", "font_33_m")}>
                {netEarning} {bettingTokenName}
              </div>
              <div className={classnames(styles.r333, "font_28", "font_52_s", "font_34_m")}>{netEarningUSD} $ </div></div>
          </div>
          <div className={styles.firstline}>
            <div className={classnames(styles.r1, "font_25", "font_8_s", "font_32_m")}>Average return per Round  </div>
            <div className={classnames(styles.r2, "font_27", "font_11_s", "font_33_m")}>
              {average} {bettingTokenName}
            </div>
            <div className={classnames(styles.r3, "font_28", "font_52_s", "font_34_m")}>{averageUSD} $</div>
          </div>
          <div className={styles.nextLine}>
            <div className={classnames(styles.r1, "font_25", "font_8_s", "font_32_m")}>Best Round </div>
            <div className={classnames(styles.r2, "font_27", "font_11_s", "font_33_m")}>
              {best} {bettingTokenName}
            </div>
            <div className={classnames(styles.r3, "font_28", "font_52_s", "font_34_m")}>{bestUSD} $</div>
          </div>
          <div className={styles.thirdLine}>
            <div className={classnames(styles.r1, "font_25", "font_8_s", "font_32_m")}>Average position entered/Round </div>
            <div className={classnames(styles.r2, "font_27", "font_11_s", "font_33_m")}>
              {avgBet} {bettingTokenName}
            </div>
            <div className={classnames(styles.r3, "font_28", "font_52_s", "font_34_m")}>{avgBetUSD} $</div>
          </div>
        </div>
      </div>
    </div>
  );
}
export default History;

function Box(props, state) {
  console.log(props.round + props.claim)
  const [str, setStr] = useState("")
  const [css, setCss] = useState(null);
  const [css2, setCss2] = useState(null);//
  const [css4, setCss4] = useState(styles.hide)
  const [css3, setCss3] = useState(false);//active or inactive
  const [flag1, setFlag1] = useState(null);//for arrow
  const [flag2, setFlag2] = useState(styles.hide);//for arrow
  const [history,setHistory]=useState(null);
  const [direction,setDirection]=useState("");
  const [str2,setStr2]=useState("");//for closing price
  const [directionCss,setDirectionCss]=useState(null);
  const [closingCss,setClosingCss]=useState(null);

  const userAddress = useSelector(state => state.userAddress);
  const web3 = useSelector(state => state.web3);
  const currentCurrency = useSelector(state => state.currentCurrency);
  const currentGroup = useSelector(state => state.currentGroup);
  var closePrice; var lockedPrice;
  if (history != null) {
    closePrice = history.closePrice;
    closePrice = (Number(closePrice).toFixed(3));
    lockedPrice = history.lockPrice;
    lockedPrice = (Number(lockedPrice).toFixed(3));
  }
  useEffect(() => {
    if(props.roundData.position==0)
    {
    setDirection("Up")
    setDirectionCss(styles.blue)
    }
    else
    {
    setDirection("Down")
    setDirectionCss(styles.orange)
    }

    if (props.result > 0) {
      setCss(styles.num);
    }
    else {
      setCss(styles.num2);
    }

    if(props.roundData.isRefundable==true)
    {
      setCss2(styles.btn4);
      setCss3(false)
      setStr("Refund");
      setCss4(styles.hide);

    }
    else{


    if (props.won == false) {
      setCss2(styles.btn2)
      setStr("Lost")
      setCss3(true)
      setCss4(styles.hide)

    }
    else {
      setCss4(null);
      if (props.claim == true) {
        setCss2(styles.btn3);
        setStr("Collected")
        setCss3(true)
      }
      else {
        setCss2(styles.btn1)
        setStr("Collect")
        setCss3(false)
      }

    }
  }


  },[]);
  useEffect(()=>{
    if(history)
    {
var difference=history.closePrice-history.lockPrice;
difference = (Number(difference).toFixed(3));
if(difference>0)
{
  setStr2(difference)
  setClosingCss(styles.blue);
}
else
{
  setStr2(difference)
  setClosingCss(styles.orange);
}
    }
  },[history])


  return (
    <div className={classnames(styles.box, "font_5", "font_8_s")}>
      <div className={styles.row11}>
        <div className={styles.col11}>
          <div className={classnames(styles.round, "font_5", "font_8_s", "font_8_m")}>
            <div>Round
              {"   " + props.round}</div>
          </div>
        </div>
        <div className={styles.col12}>
          <div className={classnames(styles.resnum, "font_5", "font_8_s", "font_8_m")}>
            <div>Result
              <span className={css}>{"   " + props.result}</span></div>
          </div>
        </div>
        <div className={styles.col14}><img src={medal} className={classnames(styles.medal, css4)}></img></div>
        <div className={styles.col13}>
          <button className={classnames(styles.button, "font_5", "font_8_s", "font_8_m", css2)} disabled={css3} onClick={() => {
            var round = [props.round];
            setStr("Collecting")
            claim(web3, userAddress, props.roundData.prediction.predictionId, round).then((result) => {
              if (result.status == false) {
                setCss2(styles.btn1)
                setStr("Collect")
                setCss3(false)
              }
              else {
                setCss2(styles.btn3);
                setStr("Collected")
                setCss3(true)
              }

            })
          }}>{str}</button>
        </div>
        <div className={styles.col15} onClick={() => {
          console.log("flag")
          if (flag1 == null) {
            setFlag1(styles.hide)
            setFlag2(null)
            console.log("roundData")
            console.log(props.roundData)
            getRoundData(props.round,props.roundData.prediction.predictionId).then((obj)=>{
console.log(obj)
setHistory(obj)
            })
          }
          else {
            setFlag2(styles.hide)
            setFlag1(null)
         
          }

        }}><i className={classnames("fas fa-angle-down", flag1)} ></i><i className={classnames("fas fa-angle-up", flag2)} ></i></div>
      </div>
      <div className={flag2}>
      <div className={classnames(styles.row11)}>
        <div className={styles.c1}>
          <div className={styles.box2}>
            <div className={styles.row111}><div className={styles.c11}><span className={styles.head}>Your History</span></div><div className={styles.c22}></div></div>
            <div className={styles.row111}><div className={styles.c11}>Your Direction</div><div className={styles.c22}><button className={classnames(styles.direction,directionCss)}>Entered {direction}</button></div></div>
            <div className={styles.row111}><div className={styles.c11}>Your Position</div><div className={styles.c22}>{props.roundData.amount}</div></div>
            <div className={styles.row111}><div className={styles.c11}>Your Winnings</div><div className={styles.c22}>{props.roundData.amountWon}</div></div>
            <div className={styles.row111}><div className={styles.c11}>Referee Bonus</div><div className={styles.c22}>{props.roundData.refereeAmount}</div></div>
          
          </div>
      </div>
        <div classname={styles.c2}>
        <div className={styles.box3}>
        <div className={styles.row111}><div className={styles.c11}><span className={styles.head}>Your History</span></div><div className={styles.c22}></div></div>
            <div className={styles.row111}><div className={styles.c11}>Closing Price</div><div className={styles.c22}>{closePrice}&nbsp;<button className={classnames(styles.closing,closingCss)}>{str2}</button></div></div>
            <div className={styles.row111}><div className={styles.c11}>Locked Price</div><div className={styles.c22}>{lockedPrice}</div></div>
            <div className={styles.row111}><div className={styles.c11}>Price Pool</div><div className={styles.c22}>{history?.totalAmount}</div></div>
            <div className={styles.row111}><div className={styles.c11}>&nbsp;&nbsp;</div><div className={styles.c22}>&nbsp;&nbsp;</div></div>
          
        </div>

        </div>
      </div>
      </div>
    </div>

  );
}
