import Modal from 'react-modal';
import React from 'react';
import styles1 from "./BetAmount.module.css";
import classnames from "classnames";
import arrow from "../assets/group_141.png";
import { getUserBettingTokenBalance, betBear, betBull, getCurrentRoundNo, isApproved, approvePrediction, getMinBetAmount } from "../utils/contractData.js";
import { useSelector, useDispatch } from 'react-redux';
import {connectMetamask} from "./conncetioncode.js";
import icon1 from "../assets/image_16.png";
import usicon from "../assets/usdc.png"
import reactDom from 'react-dom';






Modal.setAppElement('#root');


function BetAmount(props, state) {
  const paused = useSelector(state => state.paused);
  var a = false;
  const [b, setB] = React.useState(false)


  if (paused == true) {
    a = true
  }

  var round = useSelector(state => state.num);
  const dispatch = useDispatch();
  const [amount, setAmount] = React.useState(0);
  const [str, setStr] = React.useState("Bet")
  var web3 = useSelector(state => state.web3);
  var approve = useSelector(state => state.approve);
  var currentCurrency = useSelector(state => state.currentCurrency);
  React.useEffect(() => {
    if (web3 == null) {
      setStr("Connect");
    }
    else
      if (approve == false)
        setStr("Approve")
      else
        if (props.label == "Enter Up")
          setStr("Bet UP")
        else if (props.label == "Enter Down")
          setStr("Bet DOWN");

  }, [approve, web3])

  var userAddress = useSelector(state => state.userAddress);


  var css; var label = props.label; var css2;
  if (props.label == "Enter Up") {
    css = styles1.button3;
    css2 = styles1.upbg;
  }
  else {
    css = styles1.button4;
    css2 = styles1.downbg;
  }

  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal(props, state) {
    setIsOpen(true);
  }

  function afterOpenModal() {

  }

  function closeModal() {
    setIsOpen(false);
  }
  return (
    <>

      <button className={classnames(css, "font_5", "font_23_s", "font_28_m")} onClick={openModal} disabled={a} >{label}</button>
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        className={styles1.modal}


      >
        <div className={styles1.parent}>
          <img src={arrow} className={styles1.arrow} onClick={closeModal}></img></div>

        <div className={styles1.flex}>
          <div className={styles1.col1}><div className={classnames(styles1.amount, "font_5_", "font_23_s", "font_22_m")}>Amount in USDC</div></div>
          {/* <div className={styles1.col2}><img src={icon1} className={styles1.icon1}></img></div>*/}
        </div>
        <div className={styles1.align}><input type="number" className={classnames(styles1.input, "font_29", "font_23_s", "font_31_m")} onChange={(event) => { setAmount(event.currentTarget.value) }}></input><img src={usicon} className={styles1.usicon}></img></div>
        <div className={styles1.align}><button className={classnames(styles1.submit, css2, "font_29", "font_23_s", "font_31_m")} disabled={b} onClick={() => {
          
          if (str == "Connect") {
            setB(true);
            setStr("Connecting");
            connectMetamask().then((ret) => {
              setB(false)
              if (ret.error == false) {
                console.log("Checking approve")
                setStr("Checking approve")
                setB(true)
                isApproved(ret.web3, ret.acounts, currentCurrency).then((result) => {

                  setB(false)
                  
                  if (result == true) {
                    dispatch({ type: "approveChange", approve: true });
                  }
                  else{
                    setStr("Approve")
                  }

                })
                dispatch({ type: "web3Change", web3: ret.web3, userAddress: ret.acounts });

              }
              else {
                setStr("Connect")
                console.log(ret.errorMessage);
              }

            });
          }
          else if (str == "Approve") {
            setB(true);
            setStr("Approving")
            approvePrediction(web3, userAddress, currentCurrency).then((result) => {
              setB(false);
              
              if (result == true) {
                dispatch({ type: "approveChange", approve: true });
              }
              else{
                setStr("Approve")
              }
            })

          }
          else {

            if (props.label == "Enter Up") {
              setB(true)
              setStr("Betting")
              getMinBetAmount().then((minBet) => {
                if (Number(web3.utils.toWei(amount, "ether")) > Number(minBet)) {
                  getUserBettingTokenBalance(web3, userAddress, currentCurrency).then((balance) => {
                  
                    if (Number(web3.utils.toWei(amount, "ether")) < Number(balance)) {
                      
                      betBull(web3, userAddress, round, web3.utils.toWei(amount, "ether")).then((res) => {
                         setB(false);
                        
                        
                        if (res.status == true)
                          dispatch({ type: "betChange" });
                          console.log("betchange");
                        closeModal();
                        setStr("Bet UP")
                      });
                    }
                    else {
                      setB(false);
                      setStr("Bet UP")

                      console.log("you have not enough balance " + balance)
                      console.log("your bet amount is " + web3.utils.toWei(amount, "ether"))
                    }
                  });

                }
                else {
                  setB(false)
                  setStr("Bet UP")
                   console.log("amount is lower than min Bet") 
                  }
                  
              })


            }
            else {
              setB(true)
              setStr("Betting")
              getMinBetAmount().then((minBet) => {

                if (Number(web3.utils.toWei(amount, "ether")) > Number(minBet)) {
                  
                  getUserBettingTokenBalance(web3, userAddress, currentCurrency).then((balance) => {
                    if (Number(web3.utils.toWei(amount, "ether")) < Number(balance)) {
                      betBear(web3, userAddress, round, web3.utils.toWei(amount, "ether")).then((res) => {
                       
                        setB(false);
                        if (res.status == true)
                          dispatch({ type: "betChange" });
                        closeModal();
                        setStr("Bet DOWN")
                      });

                    }
                    else {
                      console.log("you have not enough balance")
                      setB(false);
                      setStr("Bet DOWN")
                    }
                  });

                }
                else { 
                  console.log("amount is lower than min Bet")
                  setB(false);
                  setStr("Bet DOWN")
                 }
              })



            }
          }


        }}>{str}</button></div>
      </Modal>
    </>
  );
}
export default BetAmount;
