import React, { useRef, useState, useEffect  } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import styles from "./Slider.module.css";
import logo from "../assets/logo.png"
// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css";

// import Swiper core and required modules
import SwiperCore, { Pagination ,Mousewheel} from "swiper/core";
import SliderPannel from "./SliderPannel";
import { propTypes } from "react-bootstrap/esm/Image";
import { useSelector} from "react-redux";
import { logDOM } from "@testing-library/react";
import groupData from "../utils/constants/groupData.js"
import  {getGroupData} from "../utils/contractData.js";
// install Swiper modules
SwiperCore.use([Pagination,Mousewheel]);

export default function Slider() {
  let [width, setWidth] = useState(document.documentElement.clientWidth);
  const num1=useSelector(state => state.num);

  useEffect(() => {
    const resizeListener = () => {
      // change width from the state object
      setWidth(document.documentElement.clientWidth)
    };
    // set resize listener
    window.addEventListener('resize', resizeListener);

    // clean up function
    return () => {
      // remove resize listener
      window.removeEventListener('resize', resizeListener);
    }
  }, []);

  /*
  var list = [];
  for (var i = 0; i < 10; i++)
    list.push(
      <SwiperSlide>
        {({ isActive, isPrev, isNext }) => <SliderPannel isactive={isActive} isnext={isNext} isprev={isPrev} activeindex={Swiper.activeIndex} slideindex={props.virtualIndex}/>}
      </SwiperSlide>
    );*/

    
    const params = {
      
      container: ".swiper-container",
    
      mousewheel: true,
     
    };
    var num=4;
    var space=20;
    var flag=true;
    if(width<=390)
    {
      num=3;
      space=250;
      flag=false;

    }
    if(width>390&&width<=768)
    {
      num=2;
      space=50;
      flag=true;

    }
    const [active,setActive]=useState(2);
    //const swiper = useRef(null);
    const [swiper, setSwiper] = useState(null);



const currentGroup= useSelector(state => state.currentGroup);
var keys = Object.keys( groupData );


const [livePs,setLivePs]=useState([]);
const [prevPs,setPrevPs]=useState([]);

useEffect(()=>{
  async function f()
  {
    
    const gData = await getGroupData(keys[currentGroup]);
    //const gData=groupData[keys[currentGroup]].ids
    
    var livePs_=[];
var prevPs_=[];
    if(gData.length>1)
    {
      for(var i=1;i<gData.length;i++)
      {

          let y= gData[i]; 
          let key=gData[i]+"p";
          let index1=i;
          let key2=gData[i]+"l";
          
         
          prevPs_.push(<SwiperSlide key={key.toString()}>{({ isActive, isPrev, isNext }) => <SliderPannel key={key.toString()} prediction={y.toString()} index1={index1.toString()} isActive={isActive} status={"prevPs"} group={currentGroup}  />}</SwiperSlide>)
          livePs_.push(<SwiperSlide key={key2.toString()}>{({ isActive, isPrev, isNext }) => <SliderPannel key={key2.toString()} prediction={y.toString()} index1={index1.toString()} isActive={isActive} status={"livePs"} group={currentGroup}  />}</SwiperSlide>)
      }
      
     
    }

    setLivePs(livePs_)
    setPrevPs(prevPs_)
  }

  f();

},[currentGroup])
var exp2=[];
if(groupData[keys[currentGroup]].ids.length==1)
exp2.push( <SwiperSlide key='1'>
{({ isActive, isPrev, isNext }) => <SliderPannel key="1" isActive={isActive} status={"expired1"} prediction={-1} group={groupData[keys[currentGroup]]}/>}
</SwiperSlide>)

  return (
    <>
   
    <div className={styles.arrows}>
<span className={styles.left} onClick={()=>{
  var index=swiper.activeIndex;
  if(index>=1)
  swiper.slideTo(index-1);
 
}
}><i className="fas fa-angle-left"></i></span><img src={logo} className={styles.logo}></img><span onClick={()=>{
  var index=swiper.activeIndex;
  if(index<=5)
  swiper.slideTo(index+1); 
}} className={styles.right}><i className="fas fa-angle-right"></i></span>
    </div>
    <Swiper  onSwiper={setSwiper}  {...params}
      slidesPerView={num}
      centeredSlides={flag}
      spaceBetween={space}
      initialSlide={active.toString()}  
      slideToClickedSlide={true}
      className={styles.swiper}
      allowPageScroll= {"vertical"}
      allowTouchMove= {false}
      mousewheel={{  
        forceToAxis: true,
       }}
  > 
  
  {exp2} 
   <SwiperSlide key='2'>
        {({ isActive, isPrev, isNext }) => <SliderPannel key="2" isActive={isActive} status={"expired2"} prediction={-1} group={groupData[keys[currentGroup]]}/>}
      </SwiperSlide>
{prevPs}
   
      <SwiperSlide key='3'>
        {({ isActive, isPrev, isNext }) => <SliderPannel key="3" isActive={isActive} status={"live"}  prediction={-1} group={groupData[keys[currentGroup]]}/>}
      </SwiperSlide>
      {livePs}
      <SwiperSlide key='4'>
        {({ isActive, isPrev, isNext }) => <SliderPannel key="4" isActive={isActive} status={"next"} prediction={-1} group={groupData[keys[currentGroup]]}/>}
      </SwiperSlide>
      <SwiperSlide key='5'>
        {({ isActive, isPrev, isNext }) => <SliderPannel key="5" isActive={isActive} status={"future1"} prediction={-1}/>}
      </SwiperSlide>
      <SwiperSlide key='6'>
        {({ isActive, isPrev, isNext }) => <SliderPannel key="6" isActive={isActive} status={"future2"} prediction={-1}/>}
      </SwiperSlide>
    </Swiper>
    </>
  );
}

