import Modal from 'react-modal';
import React from 'react';
import styles1 from "./Connect.module.css";
import classnames from "classnames";
import arrow from "../assets/group_141.png";
import icon1 from "../assets/metamask.png";
import icon2 from "../assets/TWT.png";
import icon3 from "../assets/Math.png";
import icon4 from "../assets/tocken.png";
import icon5 from "../assets/binance.jpeg";
import icon6 from "../assets/wallet.png";
import icon7 from "../assets/safe.png";
import icon8 from "../assets/coin98.png";
import { useDispatch,useSelector} from "react-redux";
import {connectMetamask, connectWalletConnect, tryReconnect, onDisconnect} from "./conncetioncode.js"
import {isApproved} from"../utils/contractData.js";

Modal.setAppElement('#root');


function Connect(props) {
  
  const dispatch = useDispatch();
  var web3=useSelector(state=>state.web3);
  var currentCurrency=useSelector(state=>state.currentCurrency);

  React.useEffect(()=> {
    tryReconnect().then((ret)=>{
      if(ret === null)
        return;
      else if(ret.error==false)
      {
        isApproved(ret.web3,ret.acounts,currentCurrency).then((approve)=>{
          if(approve==true)
          {
            dispatch({ type: "approveChange",approve:true});
          }
        })
        dispatch({ type: "web3Change",web3:ret.web3,userAddress:ret.acounts});
        closeModal();
      }
      else{
        console.log(ret.errorMessage);
      }
    });
  },[]);


var str="Connect";
if(web3!=null)
{
str="Disconnect"

}
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function afterOpenModal() {
 
  }

  function closeModal() {
    setIsOpen(false);
  }

  function handleWalletConnect(){
    connectWalletConnect().then((ret)=>{
      if(ret.error==false)
      {
        isApproved(ret.web3,ret.acounts,currentCurrency).then((approve)=>{
          if(approve==true)
          {
            dispatch({ type: "approveChange",approve:true});
          }
        })
        dispatch({ type: "web3Change",web3:ret.web3,userAddress:ret.acounts});
        closeModal();
      }
      else{
        console.log(ret.errorMessage);
      }
    });
  }

  function handleConnectMetamask() {
    connectMetamask().then((ret)=>{
      if(ret.error==false)
      {
        isApproved(ret.web3,ret.acounts,currentCurrency).then((approve)=>{
          if(approve==true)
          {
            dispatch({ type: "approveChange",approve:true});
          }
        })
        dispatch({ type: "web3Change",web3:ret.web3,userAddress:ret.acounts});
        closeModal();
      }
      else{
        console.log(ret.errorMessage);
      }
    });
  }


  return(
    <>
    <button className={classnames(styles1.button1, "font_12","font_11_s","font_22_m") } onClick={()=>{
      dispatch({ type: "showMenuChange"});
     
      if(web3==null)
      openModal();
      else{
        dispatch({ type: "web3Change",web3:null,userAddress:null});  
        onDisconnect();
      }
      }} id="yourAppElement" >
            {str}
          </button> 

          <Modal
        style={{zIndex:10}}
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        className={styles1.modal}
       
       
      >
       <img src={arrow} className={styles1.arrow} onClick={closeModal}></img>
       <div className={classnames(styles1.con,"font_21","font_50_s","font_30_m")}>Connect to a Wallet</div>
       <div className={classnames(styles1.box,"font_21","font_50_s","font_30_m")} onClick={handleConnectMetamask}><div className={styles1.col1}>Metamask</div><div className={styles1.col2}><img src={icon1} className={styles1.icon}></img></div></div>
       <div className={classnames(styles1.box1,"font_21","font_50_s","font_30_m")} onClick={handleWalletConnect}><div className={styles1.col1}>WalletConnect</div><div className={styles1.col2}><img src={icon6} className={styles1.icon}></img></div></div>

      </Modal>
      </>
  );
}
export default Connect;


