import React, { PureComponent } from 'react';
import { PieChart, Pie, Sector, Cell, ResponsiveContainer,Label } from 'recharts';


const COLORS = ["#AB2323","#7DD093" ];

export default class Example extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {numWon: this.props.numWon,
      numLoss:this.props.numLoss
    };
  }
 componentDidUpdate(prevProps) {
    if(this.props.numWon!= prevProps.numWon||this.props.numLoss!= prevProps.numLoss) // Check if it's a new user, you can also use some unique property, like the ID  (this.props.user.id !== prevProps.user.id)
    {
      this.setState( {numWon: this.props.numWon,
        numLoss:this.props.numLoss
      });
    }
  } 
  
  data = [
    { name: 'Group A', value:this.props.numWon},
    { name: 'Group B', value: this.props.numLoss},
  
  ];

  render() {
    console.log("won "+this.props.numWon);
    console.log("loss "+this.props.numLoss);
    var str="WON " +this.props.numWon+"/"+(this.props.numWon+this.props.numLoss);
    return (
      <ResponsiveContainer heigh="100%" width="100%" aspect={1} margin={{ top: 0, left: 0, right: 0, bottom: 0 }}>
      <PieChart onMouseEnter={this.onPieEnter}>
      
        <Pie
          data={[
           
            { name: 'Group A', value: this.props.numLoss}, 
            { name: 'Group B', value:this.props.numWon}
          
          ]}
          cx="50%"
          cy="50%"
          innerRadius="50%"
          outerRadius="60%"
          fill="#8884d8"
          paddingAngle={0}
          dataKey="value"
          
          
          
          
        >
            <Label textAnchor="middle" dominantBaseline="middle" position="center" fontSize="14px">{str}</Label>
          {this.data.map((entry, index) => (
            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
          ))}
        </Pie>

       
      </PieChart>
      </ResponsiveContainer>
    );
  }
}
