import Menu from "./Menu.js";
import Contact from "../landing_page/Contact.js";
import Footer from "../landing_page/Footer.js";
import styles from "./Application.module.css";
import MenuContent from "./MenuContent.js";
import React from 'react';
import { useDispatch,useSelector} from "react-redux";
import  {setPredictionIndex } from "../utils/contractData.js";

function Application() {
  
  const showMenu = useSelector(state => state.showMenu);
  const paused=useSelector(state => state.paused);

const currentRound=useSelector(state=>state.num);
  const time1=useSelector(state=>state.timeLeft);
  const timeSync=useSelector(state=>state.timeSync)
 // var counter=0;  
  const dispatch = useDispatch();
  var menucss=styles.c1;

  if(showMenu==true)
  {
menucss=styles.c1_open;
  }
  React.useEffect(() => {
  
  const interval=setInterval(function(){
    var now=timeCalc();    
    dispatch({type:"timerChange", time:now}) 
    }, 1000);
   return () => clearInterval(interval);
  });
  function timeCalc()
  {
  
    if(paused==true)
    {
      dispatch({type:"calculatedChange", calculated:true})
      return "0:0";

    }
    else{
   //counter=counter+1;
   var tt=time1-(Date.now()/1000)+timeSync
  //if(time1-counter>10)
  if(tt>10)
  {
    //var remainMinutes=parseInt((time1-counter)/60);
    var remainMinutes=parseInt((tt)/60);
    //var remainSeconds=parseInt((time1-counter)%60);
    var remainSeconds=parseInt((tt)%60);
    dispatch({type:"calculatedChange", calculated:true})
    return remainMinutes+":"+remainSeconds;

  }
  else{
   
   // if(time1-counter<10&&time1-counter>=0)
   //if(time1!=-1)
   dispatch({type:"calculatedChange", calculated:false})
    return "0:0";
    
  }
}
}
 

  return (
    <div>
      <div className={styles.row}>
        <div className={styles.c1,menucss}>
          <Menu />
        </div>
        <div className={styles.c2}>
          <MenuContent /> <div>
        <Contact />
      </div>
      <div>
        <Footer />
      </div>
        </div>
      </div>
     
    </div>
  );
}
export default Application;
