function Progress() {
  var duration = useSelector(state => state.duration);
  const timerValue = useSelector(state => state.timerValue);
  var fields=timerValue?.split(":");
  if(fields)
  {
  var min = fields[0];
  var sec = fields[1];
  var totalSec = duration - (parseInt(min) * 60 + parseInt(sec));
  var width1 = (totalSec / duration) * 100;
 
  var radius1 = 0;

  if (width1 > 90) {
    radius1 = ((width1 - 90) / 10) * 1.718;
  }
}

  return (
    <div className={styles.progressbar}>
      <div style={{ width: width1 + "%", borderBottomRightRadius: radius1 + "vw" }}></div>
    </div>

  );
}

