import styles from "./Transactions.module.css";
import classnames from "classnames";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import icon7 from "../assets/cake.png"
import icon8 from "../assets/coin2.png"
import React from "react";
import { useSelector, useDispatch} from "react-redux";
import  {getPredictionTargetToken,getPriceChartData,isClaimable,hasStopped,setPredictionIndex,getVolume24h,getPredictionCount,getTotalUsers,getPriceChangePercentage,getCurrentRoundLockTimestamp,getPredictionIntervalSeconds,subscribePredictionEvents,getCurrentRoundNo, claim} from "../utils/contractData.js";
import groupData from "../utils/constants/groupData.js"

function Transactions() {
  const dispatch = useDispatch();
  const [rowNum,setRowNum]=React.useState(0)
  const [vol24,setVol24]=React.useState(["","","","","","","","","","","","","","","","","",""]);
  const [totalUsers,setTotalUsers]=React.useState(["","","","","","","","","","","","","","","","","",""]);
  const [trend,setTrend]=React.useState(["","","","","","","","","","","","","","","","","",""]);
  const [asset,setAsset]=React.useState(["","","","","","","","","","","","","","","","","",""]);
  

  React.useEffect(() => {
    async function f1(){
var v=[];
for (const property in groupData) {
  
  var v24=await getVolume24h(property)
 v.push(v24.volume);
   }
setVol24(v);
    }
    async function f2(){
     
var u=[];
for (const property in groupData) {
var statistic =await getTotalUsers(property);

u.push(statistic)
}
setTotalUsers(u);
    }
    async function f3(){

var t=[];
for (const property in groupData) {
var statistic =await getPriceChangePercentage(property);

if(statistic>0)
t.push("UP");
else
t.push("Down");
}
setTrend(t);
    
    }
    setRowNum(Object.keys(groupData).length);
    var a=[];
    for (const property in groupData) {
      console.log(property)
      a.push(groupData[property].token)
    }
    setAsset(a)
   f1();
   f2();
   f3();
  },[]);

  dispatch({type:"tokensChange", tokens:asset});

  const currentGroup = useSelector(state => state.currentGroup);
  var flags=["false","false","false","false","false","false","false","false","false","false","false","false","false","false","false","false","false","false"];
  flags[currentGroup]="true";
  var rows=[];
  for(var i=0;i<rowNum;i++)
  {
    rows.push(<TableRow key={i} asset={asset[i]} selected={flags[i]} users={totalUsers[i]} volume={vol24[i]} trend={trend[i]} index={i}/>);
  }


  return (
    
      <table className={classnames(styles.table1,styles.table1_s)}>
        <thead>
          <tr>
            <th
              className={classnames(
                styles.th1,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              Assets 
            </th>
            <th
              className={classnames(
                styles.th11,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            ></th>
            <th
              className={classnames(
                styles.th2,
                styles.total,
                styles.thcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              Select
            </th>
            <th
              className={classnames(
                styles.th3,styles.th3_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Users
            </th>
            <th
              className={classnames(
                styles.th4,styles.th4_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Volume 24h
            </th>
            <th
              className={classnames(
                styles.th5,styles.th5_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            >
              Trend 24h
            </th>
            <th
              className={classnames(
                styles.th6,styles.th6_s,
                styles.total,
                styles.thcustom,
                "font_20","font_29_m"
              )}
            ></th>
          </tr>
        </thead>
        <tbody>
{rows}
        </tbody>
      </table>
 
  );
}

function TableRow(props,state)
{ 
  var userAddress=useSelector(state=>state.userAddress);
  //var round=useSelector(state=>state.num);
  //console.log("reward round num="+round);
    async function f(){

      dispatch({type:"currencyChange", currentCurrency:props.index});
      dispatch({type:"calculatedChange", calculated:true})
      await setPredictionIndex(props.index);
      dispatch({type:"betChange"})
      var stoped=await hasStopped();
      dispatch({type:"pausedChange", paused:stoped});
      const timestampLeft = await getCurrentRoundLockTimestamp();
      console.log(timestampLeft)
      var timer = Math.ceil(timestampLeft - (Date.now()/1000));  
      dispatch({type:"timeLeftChange", timeLeft:timer});
      var roundDuration = await getPredictionIntervalSeconds();
      console.log("duration is:"+roundDuration);
      console.log("timer is:"+timer);
      dispatch({type:"durationChange", duration:roundDuration}); 
      var currentRound=await getCurrentRoundNo();
        dispatch({type:"numChange", num:currentRound});
     
        dispatch({type:"chartDataChange", chartData:[1]});
    

      const subscribeEventHandler = (eventName,roundNo) => { 
        switch(eventName) {
        case "PAUSED": 
        dispatch({type:"pausedChange", paused:true});
        console.log("PAUSED")
        break;
        case "STARTROUND":
          console.log("STARTROUND")
          dispatch({type:"pausedChange", paused:false});
          dispatch({type:"betChange"})
        var timer = roundDuration; 
        getCurrentRoundLockTimestamp().then((timestampLeft)=>{
          timer = Math.ceil(timestampLeft - (Date.now()/1000));
        dispatch({type:"timeLeftChange", timeLeft:timer});
        getCurrentRoundNo().then((currentRound)=>{
          dispatch({type:"numChange", num:currentRound});
        });
        
        })
        
        break;

        case "REWARDSCALCULATED":
             dispatch({type:"calculatedChange", calculated:true})
        console.log("REWARDSCALCULATED")
        dispatch({type:"claculated", claculated:true});
          break;
           
        default: }
        };
      subscribePredictionEvents(subscribeEventHandler);
    }

  const dispatch = useDispatch();
  var button;
  if(props.selected=="true")
  button=<button className={classnames(styles.button_selected, "font_21","font_21_s","font_30_m")} onClick={()=>{
    dispatch({type:"groupChange", currentGroup:props.index});
 
  }}>Selected</button>
  else
  button=<button className={classnames(styles.button_select, "font_21","font_21_s","font_30_m")} onClick={
    ()=>{
      dispatch({type:"groupChange", currentGroup:props.index});
      
    }}>Select</button>

  var asseticon;
  if(props.asset.toUpperCase()=="BNB")
  asseticon=<img src={icon1} className={classnames(styles.icon1,styles.icon1_s)} ></img>
  else 
  if(props.asset.toUpperCase()=="BTC")
  asseticon=<img src={icon2} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="ETH")
  asseticon=<img src={icon3} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="APPLE"||props.asset.toUpperCase()=="APL")
  asseticon=<img src={icon4} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="GOOGLE"||props.asset.toUpperCase()=="GOOGL")
  asseticon=<img src={icon5} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="TESLA"||props.asset.toUpperCase()=="TSL"||props.asset.toUpperCase()=="TSLA")
  asseticon=<img src={icon6} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="CAKE")
  asseticon=<img src={icon7} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else
  asseticon=<img src={icon8} className={classnames(styles.icon1,styles.icon1_s)}></img>

  return(
  <tr>
            <td
              className={classnames(
                styles.td1,
                styles.total,
                styles.tdcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              {asseticon}
            </td>
            <td
              className={classnames(
                styles.td11,
                styles.total,
                styles.tdcustom,
                "font_20","font_20_s","font_29_m"
              )}
            >
              <span className={styles.txt}>{props.asset}</span>
            </td>
            <td
              className={classnames(
                styles.td2,
                styles.total,
                styles.tdcustom,
                "font_21"
              )}
            >
{button}
              
            </td>
            <td
              className={classnames(
                styles.td3, styles.td3_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.users}</div>
              <div className={classnames( styles.nextline)}>
                Total users
              </div>
            </td>
            <td
              className={classnames(
                styles.td4, styles.td4_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.volume}</div>
              <div className={classnames( styles.nextline)}>
                Dollars
              </div>
            </td>
            <td
              className={classnames(
                styles.td5, styles.td5_s,
                styles.total,
                styles.tdcustom,
                "font_20","font_29_m"
              )}
            >
              <div className={styles.firstline}>{props.trend}</div>
              <div className={classnames( styles.nextline)}>
                &nbsp;
              </div>
            </td>
            <td className={classnames(styles.td6, styles.td6_s, styles.total,"font_6_m")}>
              .<br />.<br />.
            </td>
          </tr>);
}
export default Transactions;


