import { react } from "@babel/types";
import styles from "./Drop.module.css"
import {useState,useEffect,useRef} from "react";
import classnames from "classnames";
import { useSelector, useDispatch} from "react-redux";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import icon7 from "../assets/cake.png"
import icon8 from "../assets/coin2.png"

function Drop()
{
    
    const dispatch = useDispatch();
    const [open,setOpen]=useState(styles.hide);
    const wrapperRef = useRef(null);
    const tokens=useSelector(state=>state.tokens)
    var items=[];
    if(tokens!=0){
        console.log(tokens.length)
    for(var i=0;i<tokens.length;i++)
    {   
        var icon=null;
        var token =tokens[i];
  if(token.toUpperCase()=="BNB")
  icon=icon1; 
  else 
  if(token.toUpperCase()=="BTC")
  icon=icon2;
  else if(token.toUpperCase()=="ETH")
  icon=icon3;
  else if(token.toUpperCase()=="APPLE"||token.toUpperCase()=="APL")
  icon=icon4;
  else if(token.toUpperCase()=="GOOGLE"||token.toUpperCase()=="GOOGL")
  icon=icon5;
  else if(token.toUpperCase()=="TESLA"||token.toUpperCase()=="TSL"||token.toUpperCase()=="TSLA")
  icon=icon6;
  else if(token.toUpperCase()=="CAKE")
  icon=icon7;
  else
  icon=icon8;
items.push(<MenuItem key={i} cc={i} icon={icon} token={token} open={setOpen}/>);

    }
}

    const handleClickOutside = event => {
        if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
          setOpen(styles.hide);
        }
      };
    useEffect(() => {
       
        

        // Bind the event listener
        document.addEventListener("click", handleClickOutside,false);
        return () => {
            // Unbind the event listener on clean up
            document.removeEventListener("click", handleClickOutside,false);
        };
    }, []);

 console.log(items);
 
    return(
        <div ref={wrapperRef} className={styles.drop}>
        <div className={styles.header} onClick={()=>{
            if(open==styles.hide)
             setOpen(null);
             else
             setOpen(styles.hide)
        }    
        }>
            Choose Your Asset&nbsp;&nbsp;&nbsp;<i className={"fas fa-angle-down"}></i>
        </div>
        <div className={classnames(styles.body,open)}>
            {items}
           
        </div>
        </div>
    );

}

function MenuItem(props){
    const dispatch = useDispatch();
    return(
        <>
        <div className={styles.Item} onClick={()=>{
            props.open(styles.hide);
            dispatch({type:"groupChange", currentGroup:props.cc});
       }    
       }><div className={styles.c1}><img src={props.icon} className={styles.icon}></img></div><div className={styles.c2}>{props.token}</div></div>
       <hr className={styles.hr}></hr></>
    )
}
export default Drop;