export const getTotalProfit = async (_userAddress) => {
    let apiURL = apiBase + "userGlobalHistory" + apiId + `&user=${_userAddress}`;

    const data = await fetch(apiURL);
    const history = await data.json();
    const rounds = history.result;

    let netEarning = new BigNumber(0);

    let roundsFiltered = [];

    const bettingTokenPriceCache = {};

    for(let i =0; i< rounds.length; i++){
        const round = rounds[i];

        const pid = rounds[i].prediction.predictionId;
        const bettingTokenAddress = await getPredictionBettingTokenAddress(pid);
        let bettingTokenUSDPrice;
        if(bettingTokenPriceCache[bettingTokenAddress])
            bettingTokenUSDPrice = bettingTokenPriceCache[bettingTokenAddress];
        else
            bettingTokenUSDPrice = await getBettingTokenUSDPrice(bettingTokenAddress);

        const predictionContract = await getPredictionContract(pid);

        round.isRefundable = false;

        let refundable = false;
        //check uncalculated rounds and set as refundable if the rounds are already over
        if(!round.calculated) {
            refundable = await predictionContract.methods.refundable(round.currentEpoch, _userAddress).call();
        }
        
        if(refundable){}
        else if(round.won)
        {
            const profit = BigNumber(round.amountWon).plus(round.refereeAmount).plus(round.stakingAmount).minus(round.amount);
            netEarning = netEarning.plus(profit.multipliedBy(bettingTokenUSDPrice));
        }
        else
        {
            netEarning = netEarning.minus(BigNumber(round.amount).multipliedBy(bettingTokenUSDPrice));
        }     
    }
    
    netEarning = netEarning.dividedBy(BIG_TEN.pow(18));
    
    return netEarning.toFixed(2);
}