function reducer(state = { menuItem: "Dashboard", currentCurrency: 0, currentGroup: 0, timeLeft: -1, duration: 0, timerValue: null, timeSync: 0, page: "LandingPage", num: 0, showMenu: false, chartData: [], web3: null, userAddress: null, approve: false, paused: false, claim: false, calculated: true, bet: false, tokens: [] }, action) {
  //currenctCurrency=prediction
  //num=currentround  
  switch (action.type) {
    case "menuContentChange":
      return {
        ...state,
        menuItem: action.menuItem
      };

    case "pageChange":
      return {
        ...state,
        page: action.page
      };

    case "currencyChange":
      return {
        ...state,
        currentCurrency: action.currentCurrency
      };
    case "groupChange":
      return {
        ...state,
        currentGroup: action.currentGroup
      };
    case "timerChange":
      return {
        ...state,
        timerValue: action.time
      };
    case "numChange":
      return {
        ...state,
        num: action.num
      };
    case "showMenuChange":
      return {
        ...state,
        showMenu: !state.showMenu
      };

    case "chartDataChange":
      return {
        ...state,
        chartData: action.chartData
      };
    case "timeLeftChange":
      return {
        ...state,
        timeLeft: action.timeLeft
      };
    case "durationChange":
      return {
        ...state,
        duration: action.duration
      };
    case "web3Change":
      return {
        ...state,
        web3: action.web3,
        userAddress: action.userAddress
      };

    case "approveChange":
      return {
        ...state,
        approve: action.approve
      };

    case "pausedChange":
      return {
        ...state,
        paused: action.paused
      };

    case "claimChange":
      return {
        ...state,
        claim: action.claim
      };

    case "calculatedChange":
      return {
        ...state,
        calculated: action.calculated
      };
    case "betChange":
      return {
        ...state,
        bet: !state.bet
      };
    case "tokensChange":
      return {
        ...state,
        tokens: action.tokens
      };
    case "timeSyncChange":
      return {
        ...state,
        timeSync: action.timeSync
      };

    default:
      return state;
  }
}

export default reducer;